<template>
  <div class="page-card-content">
    <div class="top-area">
      <el-descriptions>
        <el-descriptions-item label="角色名称">{{ row.name }}</el-descriptions-item>
        <el-descriptions-item label="角色描述">{{ row.desc }}</el-descriptions-item>
      </el-descriptions>
    </div>
    <div class="main-area">
      <div class="content-left">
        <div class="platform">平台信息</div>
        <div class="list">
          <div
            v-for="(item, index) in platformList"
            :key="index"
            class="plat-item"
            :class="index == clickIndex ? 'active' : ''"
            @click="getMenu(item.id, index)"
          >
            <div>
              <img :src="item.icon" />
            </div>
            <span> {{ item.name }}</span>
          </div>
        </div>
      </div>
      <div class="content-right" v-loading="loading">
        <div class="platform pl24">授权列表</div>
        <div>
          <div class="menu-name">
            <img src="../../assets/img/Group1645.png" />
            <span>{{ clickName }}</span>
          </div>
          <div class="tree">
            <el-tree
              :data="menuList"
              :props="defaultProps"
              node-key="id"
              show-checkbox
              default-expanded-keys
              :default-checked-keys="checkTree"
              @check="handleClick"
              ref="tree"
            ></el-tree>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from '@/api/index.js'

export default {
  filters: {},
  data() {
    return {
      loading: false,
      platformList: [],
      platformIcon: [
        {
          icon: require('../../assets/img/Group 1649.png')
        },
        {
          icon: require('../../assets/img/Group1646.png')
        },
        {
          icon: require('../../assets/img/Subtract.png')
        },
        {
          icon: require('../../assets/img/Group 1650.png')
        },
        {
          icon: require('../../assets/img/Vector (3).png')
        }
      ],
      menuList: [
        {
          label: '一级 1',
          children: [
            {
              label: '二级 1-1',
              children: [
                {
                  label: '三级 1-1-1'
                }
              ]
            }
          ]
        },
        {
          label: '一级 2',
          children: [
            {
              label: '二级 2-1',
              children: [
                {
                  label: '三级 2-1-1'
                }
              ]
            },
            {
              label: '二级 2-2',
              children: [
                {
                  label: '三级 2-2-1'
                }
              ]
            }
          ]
        },
        {
          label: '一级 3',
          children: [
            {
              label: '二级 3-1',
              children: [
                {
                  label: '三级 3-1-1'
                }
              ]
            },
            {
              label: '二级 3-2',
              children: [
                {
                  label: '三级 3-2-1'
                }
              ]
            }
          ]
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      row: {},
      clickIndex: 0,
      clickName: '',
      addMenuIds: []
    }
  },
  created() {
    this.row = JSON.parse(this.$route.query.row)
  },
  mounted() {
    this.getPlatformList()
  },
  methods: {
    handleClick() {
      this.addMenuIds = this.$refs.tree.getCheckedKeys()
      API.getRolesAuth({ addMenuIds: this.addMenuIds, roleId: parseInt(this.row.id) })
        .then(res => {
          this.loading = true
          if (res.status == 200) {
            console.log(res.message)
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    getMenu(id, index) {
      this.checkTree = []
      this.clickIndex = index
      this.clickName = this.platformList[index].name
      API.getMenuList({ platformId: id, roleId: parseInt(this.row.id) })
        .then(res => {
          this.loading = true
          if (res.status == 200) {
            this.menuList = res.data
            this.menuList.forEach(item => {
              if (item.roleAuth) {
                this.checkTree.push(item.id)
              }
              if (item.children && item.children.length > 0) {
                item.children.forEach(ccitem => {
                  if (ccitem.roleAuth) {
                    this.checkTree.push(ccitem.id)
                  }
                })
              }
            })
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    getPlatformList() {
      API.getPlatformList().then(res => {
        if (res.status == 200) {
          this.platformList = res.data
          if (this.platformList.length > 0) {
            this.platformList.forEach((item, index) => {
              item.icon = this.platformIcon[index % 5].icon
            })
            this.getMenu(this.platformList[0].id, 0)
          }
        } else {
          this.$message.error(res.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.active {
  background: #ffffff;
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.059);
  border-radius: 20px;
}
.main-area {
  display: flex;
  .content-left {
    width: 360px;
    height: 739px;
    border: 1px solid #eaebf0;
    margin-right: 24px;
    overflow: auto;

    .list {
      padding: 10px;
    }
    .plat-item {
      padding: 26px 20px;
      font-family: 'PingFang SC';
      font-style: normal;
      font-weight: bold;
      font-size: 15px;
      color: #2f2f2f;
      display: flex;
      align-items: center;
      cursor: pointer;
      margin-bottom: 10px;
      &:hover {
        background: #ffffff;
        box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.059);
        border-radius: 20px;
        color: #2160ec;
      }

      div {
        width: 40px;
        height: 26px;
        background-image: url(../../assets/img/Group1645.png);
        background-size: 100% 100%;
        padding: 5px 13px;
        box-sizing: border-box;
        margin-right: 32px;
      }
    }
  }
  .content-right {
    flex: 1;
    border: 1px solid #eaebf0;
    margin-right: 24px;
    .menu-name {
      display: flex;
      align-items: center;
      padding: 38px 24px;
      box-sizing: border-box;
      img {
        width: 26px;
        height: 18px;
        margin-right: 16px;
      }
      span {
        font-weight: bold;
        font-size: 16px;
        color: #2f2f2f;
      }
    }
    .tree {
      margin-left: 60px;
    }
  }
  .platform {
    height: 42px;
    line-height: 42px;
    background: #f7f8fb;
    font-size: 15px;
    color: #2f2f2f;
    padding-left: 32px;
  }
  .pl24 {
    padding-left: 24px;
  }
}
</style>
